.listItem {
  font-family: 'Raleway', sans-serif;
  font-size: 1.5em;
  padding-bottom: 15px;
  display: flex;
  width: 100%;
  align-items: top;
  line-height: 1.5em;
}
.listItem span {
  padding-left: 10px;
  max-width: 75%;
}
.btnClose {
  vertical-align:text-bottom;
  margin-top: 10px;
  margin-left: 10px;
}
.checkbox {
  width: 40px;
  display: none;
}
.checked{
  width: 40px;
  font-size: 1.5em;
  color: var(--green500);
  vertical-align: bottom;
}
.unchecked{
  width: 40px;
  font-size: 1.5em;
  color: var(--green500);
  vertical-align: bottom;
}
/* Tablet viewport */
@media screen and (min-width: 521px) and (max-width: 1024px) {
  .listItem span {
     font-size: 1em;
  }
}
/* Mobile viewport */
@media screen and (max-width: 520px) {
  .listItem span {
     font-size: .75em;
     line-height: 1.5em;
  }
}